.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

div{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

table,td,th,tr{
  text-align: center;
  border-bottom: 1px solid black;
  border-collapse: collapse;
  padding: 20px 80px;
}

form{
  margin: 20px;
}

input{
  margin-bottom: 20px;
  border-radius: 7px;
  padding: 10px 80px;
}

label{
  float: left;
}